import { getAllPosts } from '@/lib/posts';
import Link from 'next/link';
import { format } from 'date-fns';
import { ko } from 'date-fns/locale';

export const metadata = {
    title: 'Blog',
    description: '생명과 데이터를 잇는 기술 이야기',
};

export default function BlogPage() {
    const posts = getAllPosts();

    // 날짜별 그룹화 (Optional) 하지만 Scriptor는 그냥 깔끔한 리스트가 어울림

    return (
        <div className="container-minimal">
            <div className="mb-16 border-b border-[var(--border)] pb-8">
                <h1 className="text-4xl md:text-5xl font-extrabold mb-4 font-sans tracking-tight">Writing</h1>
                <p className="text-lg text-[var(--foreground-secondary)] font-serif italic">
                    생각과 배움의 조각들을 모아둡니다.
                </p>
            </div>

            <div className="space-y-16">
                {posts.map((post) => (
                    <article key={post.slug} className="group">
                        <header>
                            <div className="flex items-center gap-3 text-sm text-[var(--foreground-secondary)] mb-2 font-sans">
                                <time dateTime={post.date} className="font-medium">
                                    {format(new Date(post.date), 'MMMM d, yyyy', { locale: ko })}
                                </time>
                                <span className="w-1 h-1 rounded-full bg-[var(--border)]"></span>
                                <span className="text-[var(--primary)] uppercase tracking-wider text-xs font-bold">
                                    {post.category[0]}
                                </span>
                            </div>

                            <Link href={`/blog/${post.slug}`}>
                                <h2 className="text-3xl md:text-4xl font-bold mb-4 group-hover:text-[var(--primary)] transition-colors font-sans leading-tight">
                                    {post.title}
                                </h2>
                            </Link>
                        </header>

                        <div className="text-[var(--foreground-secondary)] leading-relaxed font-serif text-lg mb-4">
                            {post.description}
                        </div>

                        <Link href={`/blog/${post.slug}`} className="text-sm font-bold border-b-2 border-[var(--border)] hover:border-[var(--primary)] pb-1 transition-all font-sans">
                            Read article
                        </Link>
                    </article>
                ))}

                {posts.length === 0 && (
                    <div className="py-20 text-center text-[var(--foreground-secondary)] italic font-serif">
                        아직 작성된 글이 없습니다.
                    </div>
                )}
            </div>
        </div>
    );
}
